import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Div} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();

const Stats = (props,go,Group,ListItem,fetchedUser,Avatar,profilename,profilesurname,profileimg) => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={props.go} data-to="home">
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			Статистика
		</PanelHeader>
		<Div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
			Данный раздел в разработке.
		</Div>
		
			Ваш ид: { props.vkid  }
			Ваша Имя и фамилия: { props.profilename  } { props.profilesurname  }
			Ваша картинка: <img src={ props.profileimg  }/>
	</Panel>

);


export default Stats;
